.y-container {
  background-color: #fff;
  min-width: 1020px;
}

.y-container_content {
    max-width: 960px;
    margin: 0 auto;
    padding: 0 15px;
}

.homepage-hero {
  padding-top: 30px;
  height: 570px;
  background-color: #333;
  background-size: cover;
  background-position: 50%;
  background-image: url('../../images/top_photo.jpg');
}

.arrange--18 {
  margin-left: -18px;
  margin-right: -18px;
  border-collapse: separate;
  border-spacing: 18px 0;
}

.arrange {
  display: table;
  min-width: 100%;
  table-layout: auto;
}

.arrange--18>.arrange_unit, .ie .arrange--18.arrange--equal {
    border-collapse: collapse;
    border-spacing: 0 0;
}

.arrange--18>.arrange_unit {
    margin-left: 9px;
    margin-right: 9px;
}

.arrange_unit--fill {
    width: 100%;
}

.arrange_unit {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: table-cell;
  vertical-align: top;
}

.nowrap {
    white-space: nowrap;
}


.header-nav {
    display: inline-block;
}

ol, ul {
    list-style: none;
}

.header-nav_item {
    display: inline-block;
    margin-right: 2px;
}

.header-nav_link {
  border-radius: 0;
  border-bottom: 2px solid transparent;
  padding-bottom: 8px;
  color: #fff;
  display: block;
  padding: 9px 12px 10px;
  font-size: 14px;
  line-height: 1.28571em;
  font-weight: 700;
  background: none;
}

.hero-header_nav .header-nav_link {
    border-radius: 0;
    border-bottom: 2px solid transparent;
    padding-bottom: 8px;
}

.hero-header_nav .header-nav_link, .hero-header_nav .header-nav_text {
    color: #fff;
    display: block;
    padding: 9px 12px 10px;
    font-size: 14px;
    line-height: 1.28571em;
    font-weight: 700;
    background: none;
}

.homepage-hero_logo {
    background-image: url(../../images/yelp_logo.png);
    background-size: 160px 80px;
    width: 160px;
    height: 80px;
    padding: 0;
    margin: 0 auto 48px;
    background-repeat: no-repeat;
}

.homepage-hero_logo a {
    display: block;
    width: 100%;
    height: 100%;
    text-indent: -1000em;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



